import React from 'react';
import {  View, ScreenSpinner, Panel, Epic, Button, PanelHeader, Panar, Avatar, Div, HeaderButton, Cell, Snackbar, Gallery, Group, List} from '@vkontakte/vkui';
import './Profile.css'
// Компонент профиля
class Profile extends React.Component {
	constructor(props) {
       super(props);

       this.state = {
          fetchedUser:this.props.fetchedUser
     }
  }

	render() {
		return(
                <Div id="profile-container" align="center">
                  <Group id="avatar-container">
                     <Avatar src={this.state.fetchedUser.photo_200} size={200}/>
                  </Group>

                  <Group id="name-container">
                    <Cell id="name">{this.state.fetchedUser.first_name+" "+this.state.fetchedUser.last_name}</Cell>
                  </Group>

                <Group class="container" title="Общая информация" id="data-container">
                  <List>
                     <Cell>{this.state.fetchedUser.country.title}</Cell>
                     <Cell>{this.state.fetchedUser.city.title}</Cell>
                  </List>
                </Group>
                
               
                  <Group class="container" title="Достижения" id="achievements-container">
                     <List>
                          <Cell indicator="1 lvl">Первопроходец</Cell>
                          <Cell indicator="1 lvl">Мастер квестов</Cell>
                     </List>
                  </Group>
               

            </Div>
		);           
	}
}

export default Profile;